.promos {
    position: relative;

    display: flex;
    flex-direction: column;
    margin-top: 180px;

    &__promotions {
        display: flex;
        justify-content: space-between;
    }

    &__title {          
        color: #757aa5;
        font-weight: 200;
        font-style: normal;
        font-size: 4.5vw;
        font-family: $Mont;
        line-height: 1.3;
    }

    &__text {
        width: 22vw;
        margin: 100px 0 110px;

        color: #888794;
        font-weight: 500;
        font-style: normal;
        font-size: large;
        font-family: $Mont;
        line-height: 1.3;
    }

    &__products {
        display: flex;
        gap: 2rem;
        align-items: flex-end;
        align-self: center;
        justify-content: space-between;

        width: 100%;
        margin-top: 270px;
    }

    &__branch {
        position: absolute;
        top: 10%;
        left: -20%;
        z-index: -1;
        width: 57vw;
        transform: rotate(10deg);
    }
}